import { Component, OnInit, ViewChild } from '@angular/core';
import { Subject } from 'rxjs';
import { NgForm } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { SetupService } from 'src/app/services/setup.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';
import { config } from 'src/environments/environment';
import { TellerService } from 'src/app/services/teller.service';


@Component({
  selector: 'app-teller-placeorder',
  templateUrl: './teller-placeorder.component.html',
  styleUrls: ['./teller-placeorder.component.css']
})
export class TellerPlaceorderComponent implements OnInit {
  @ViewChild('orderForm') public any: NgForm; edit: boolean;
  items: any[]; loc: any[]; food: any[];  cart: any[] = []; cat: any;
  order: any; checkout: boolean; total: number; user: string; res: any;
  public img = config.imageUrl + 'food/';
  constructor(private _setup: SetupService, private _api: TellerService, private _use: NavComponent) { }

  ngOnInit() {
    this.totalPrice();
    this.formReset();
    this.getAllFood();
    this.user = this._use.active.unique_name;
  }

  getAllFood() {
    this._setup.getFoods().subscribe((data: any) => {
      this.food = data;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
    this._setup.getLocations().subscribe((data: any) => {
      this.loc = data;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  postOrder() {
    this._api.getCode('Order').subscribe((data: any) => {
      this.order.orderno = data; this.addOrder(this.order);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Generate New Order No');
    });
  }

  addOrder(order) {
    this._api.postOrder(order).subscribe((data: any) => {
      this.res = data; this.sendSms(data);
      // this.formReset(this.any); this.cancel();
      this._use.showMsg('success', 'Order Added', 'Order Placed Successfully And User has been sent Order No');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Add New Order');
    });
  }

  sendSms(order) {
    this._setup.postSmslog(order).subscribe((data: any) => {
      this.res = data;
      this._use.showMsg('success', 'Order Added', 'Order Placed Successfully And User has been sent Order No');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Sms Fail', 'Unable to Send OrderNo to Customer');
    });
  }

  addCart(item: any) {
    // console.log(item);
    const value = this.check(item);
    if (!value) {
      this.init(item);
      this.cart.push(this.cat);
    }
    // this.totalPrice();
  }

  check(val) {
    for (let i = 0; i < this.cart.length; i++) {
      if (this.cart[i].foodid === val.foodid) {
        this.cart[i].quantity++;
        // this.cart[i].amount += (val.price - ((val.price * val.discount) / 100));
        return true;
      }
    }
    return false;
  }

  totalPrice(): number {
    this.total = 0;
    for (let i = 0; i < this.cart.length; i++) {
      this.total += (this.cart[i].amount * this.cart[i].quantity);
    }
    return this.total;
  }

  cartTotal(): number {
    let quantity = 0;
    this.cart.forEach((e: any) => {
      quantity = quantity + e.quantity;
    });
    return quantity;
  }

  checkOut() {
    console.log(this.cart);
    this.order.vat = 0;
    this.order.price = this.total,
    this.order.total = this.total + this.order.vat;
    // this.order.orderlist = this.cart;
    this.checkout = true;
  }

  cancel() {
    this.cart = [];
    this.totalPrice();
    this.checkout = false;
    this.formReset();
  }

  formReset(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.order = {
        fullname: '', mobile: '', email: 'none@acyst.tech', price: 0, vat: 0, total: 0,
        locationid: '1', address: 'N/A', source: 'Software',
        orderno: 'None', method: 'cash', status: 'pending',
        userid: this._use.active.Id, date: this._use.date, orderlist: this.cart };
    }
  }

  init(d) {
    this.cat = {
      foodid: d.foodid, name: d.name, img: d.image, description: d.description,
      quantity: 1, amount: d.price - ((d.price * d.discount) / 100), date: new Date() };
  }
}
