import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { SetupService } from 'src/app/services/setup.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';

@Component({
  selector: 'app-setup-discount',
  templateUrl: './setup-discount.component.html',
  styleUrls: ['./setup-discount.component.css']
})
export class SetupDiscountComponent implements OnInit {
  @ViewChild('discountForm') public any: NgForm;
  edit: boolean; list: boolean; dct: any; discount: any[]; res: any; loc: any[];
  constructor(private _setup: SetupService, private _use: NavComponent) { }

  ngOnInit() {
    this.formReset();
    this.getAllDiscount();
  }

  getAllDiscount() {
    this._setup.getDiscounts().subscribe((data: any) => {
      this.discount = data;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
    this._setup.getLocations().subscribe((data: any) => {
      this.loc = data;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  editDt(data) {
    // this.dct = this.discount.find(s => s.discountid === id).
    this.dct = data;
    this.edit = true; this.list = true;
  }
  delDt(dat) {
    this._setup.delDiscount(dat.discountid).subscribe((data: any) => {
      this.res = data; this.discount.slice(dat);
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  postDiscount() {
    this._setup.postDiscount(this.dct).subscribe((data: any) => {
      this.res = data; this.discount.push(data);
      this.formReset(this.any); this.list = false;
      this._use.showMsg('success', 'Discount Added', 'Added to list Successfully');
      }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Add New Discount');
    });
  }

  putDiscount() {
    this.dct.muserid = this._use.active.Id; this.dct.mdate = new Date();
    this._setup.putDiscount(this.dct.discountid, this.dct).subscribe((data: any) => {
      this.res = data; this.getAllDiscount();
      this.formReset(this.any); this.list = false;
      this._use.showMsg('success', 'Discount Updated', 'Details Updated Successfully');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Updated Fail', 'Unable to Update Details');
    });
  }

  formReset(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.dct = {
        discountid: 0, name: '', mobile: '', locationid: null, percent: 0,
        userid: this._use.active.Id, date: new Date(), muserid: null, mdate: null
      };
    }
  }
  listView() {
    this.list = true; this.edit = false;
  }
  back() {
    this.list = false;
  }

}
