import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { InventoryService } from 'src/app/services/inventory.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-addstock',
  templateUrl: './addstock.component.html',
  styleUrls: ['./addstock.component.css']
})

export class AddstockComponent implements OnInit {
  @ViewChild('stockForm') public any: NgForm; edit: boolean;
  stock: any; id: any; res: any; logs: any[]; items: any[];
  constructor(private _api: InventoryService, private _use: NavComponent, private _nav: Router, private _route: ActivatedRoute) { }

  ngOnInit() {
    this.formReset();
    this.getAll();
  }

  getAll() {
    this.id = this._route.snapshot.paramMap.get('id');
    this.getStock(this.id); // console.log(this.id);
    this._api.getItems().subscribe((data: any) => {
      this.items = data;
      this.stock.itemid = this.id;
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Item Fetching Fail', 'Unable to Fetch List of Item in Database');
    });
  }

  getStock(id: any) {
    if (id && id !== 0) {
      this.edit = false;
      this._api.getStockLog(id).subscribe((data: any) => {
        this.logs = data;
      }, (err: HttpErrorResponse) => {
        this._use.showMsg('error', 'Stock Log Fetching Fail', 'Unable to Fetch Stock Log for Selected item');
      });
    } else {
      this.edit = false;
      this._nav.navigate(['/inventory/items']);
    }
  }

  postStock() {
    this._api.postStock(this.stock).subscribe((data: any) => {
      this.res = data; this.formReset(this.any);
      this._use.showMsg('success', 'Stock Added', 'Added to list Successfully');
      this._nav.navigate(['/inventory/items']);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Add New Stock');
    });
  }

  putStock() {
    this.stock.muserid = this._use.active.Id; this.stock.mdate = new Date();
    this._api.putStock(this.stock.stockid, this.stock).subscribe((data: any) => {
      this.res = data; this.formReset(this.any);
      this._use.showMsg('success', 'Stock Updated', 'Details Updated Successfully');
      this._nav.navigate(['/inventory/stock']);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Updated Fail', 'Unable to Update Details');
    });
  }

  formReset(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.stock = { stockid: 0, itemid: null, quantity: null, userid: this._use.active.Id, date: new Date(), muserid: null, mdate: null };
    }
  }

}
