import { Component, OnInit } from '@angular/core';
import { HttpTransportType, HubConnectionBuilder } from '@aspnet/signalr';
import { NavComponent } from 'src/app/traits/nav/nav.component';
import { config } from 'src/environments/environment';
import { ToastrService } from 'ngx-toastr';
import { TellerService } from 'src/app/services/teller.service';
import { HttpErrorResponse } from '@angular/common/http';
// import { setRootDomAdapter } from '@angular/platform-browser/src/dom/dom_adapter';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.css']
})
export class OrderListComponent implements OnInit {
  ready: any[] = []; orders: any[] = [];
  app = config; type = HttpTransportType.LongPolling;
  private _hubConnection = new HubConnectionBuilder().withUrl(this.app.ordUrl,
    { transport: this.type }).build();
  constructor(private toastr: ToastrService, private _api: TellerService) { }

  ngOnInit() {
    this.getData();
    this.getAllOrder();
    // this.setdata();
    this._hubConnection.start().then(() => {
      // console.log('Hub connection started');
      this.getOrders();
      }).catch(err => {
      console.log('Error establishing connection');
    });
  }

  public getData(): void {
    this._hubConnection.on('pending', (data: any[]) => {
      console.log(data);
      this.orders = data;
    });
    this._hubConnection.on('ready', (data: any[]) => {
      console.log(data);
      this.ready = data;
    });
    this._hubConnection.on('send', (data: any) => {
      console.log(data);
      this.showMsg('success', 'Broadcast', data);
    });
    // this._hubConnection.on('Error', (data: any) => {
    //   this.showMsg('error', 'Error Message', 'Message:' + data);
    // });
  }

  public getOrders(): void {
    this._hubConnection.invoke('Orders');
    this._hubConnection.invoke('SendAsync', 'Orders Dashboard');
  }

  showMsg(type, title, message) {
    if (type === 'success') {
      this.toastr.success(message, title);
    } else if (type === 'warning') {
      this.toastr.warning(message, title);
    } else if (type === 'error') {
      this.toastr.error(message, title);
    }
  }

  confirmOrder(id) {
    this._api.confirmOrder(id).subscribe((data: any) => {
      this.sendSms(data); this.getAllOrder();
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  sendSms(order) {
    this._api.postConfirmSms(order).subscribe((data: any) => {
      this.showMsg('success', 'Order Collected', 'Order Confirm Successfully And User has been sent Confirmation');
    }, (err: HttpErrorResponse) => {
      this.showMsg('error', 'Sms Fail', 'Unable to Send Confirmation to Customer');
    });
  }

  getAllOrder() {
    this._api.getOrderScreen().subscribe((data: any) => {
      console.log(data);
      this.ready = data.ready; this.orders = data.pending;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

}
