import { Component, OnInit } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { InventoryService } from 'src/app/services/inventory.service';

@Component({
  selector: 'app-inventory-item',
  templateUrl: './inventory-item.component.html',
  styleUrls: ['./inventory-item.component.css']
})
export class InventoryItemComponent implements OnInit {
  items: any[]; res: any;
  constructor(private _ser: InventoryService) { }

  ngOnInit() {
    this.getAllItem();
  }

  getAllItem() {
    this._ser.getItems().subscribe((data: any) => {
      this.items = data;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  delItem(dat) {
    this._ser.delItem(dat.itemid).subscribe((data: any) => {
      this.res = data; this.items.slice(dat);
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

}
