import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { InventoryService } from 'src/app/services/inventory.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';

@Component({
  selector: 'app-inventory-supply',
  templateUrl: './inventory-supply.component.html',
  styleUrls: ['./inventory-supply.component.css']
})
export class InventorySupplyComponent implements OnInit {
  @ViewChild('saleForm') public any: NgForm; edit: boolean;
  sal: any; res: any; logs: any[]; items: any[];
  constructor(private _api: InventoryService, private _use: NavComponent, private _nav: Router, private _route: ActivatedRoute) { }

  ngOnInit() {
    this.formReset();
    this.getAll();
  }

  getAll() {
    this.edit = false;
    this._api.getItems().subscribe((data: any) => {
      this.items = data;
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Item Fetching Fail', 'Unable to Fetch List of Item in Database');
    });
    this._api.getSales().subscribe((data: any) => {
      this.logs = data;
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Supply Log Fetching Fail', 'Unable to Fetch Stock Log for Selected item');
    });
  }

  amount() {
    this.sal.amount = this.sal.quantity * this.sal.price;
  }

  editSup(id) {
    this.sal = this.logs.find(s => s.saleid === id);
    this.edit = true;
  }

  getItem(id) {
    this._api.getItem(id).subscribe((data: any) => {
      this.sal.price = data.saleprice;
      this._use.showMsg('success', 'Item Detail Fetching', 'Item Details Fetched Successfully');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Details Fetching', 'Unable to get Item Details');
    });
  }

  postSupply() {
    this._api.postSale(this.sal).subscribe((data: any) => {
      this.res = data; this.formReset(); this.getAll();
      this._use.showMsg('success', 'Supply Added', 'Added to list Successfully');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Add New Supply');
    });
  }

  putSupply() {
    this.sal.muserid = this._use.active.Id; this.sal.mdate = new Date();
    this._api.putSale(this.sal.saleid, this.sal).subscribe((data: any) => {
      this.res = data; this.formReset(); this.getAll();
      this._use.showMsg('success', 'Supply Updated', 'Details Updated Successfully');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Updated Fail', 'Unable to Update Details');
    });
  }

  formReset(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.sal = {
        saleid: 0, itemid: '', price: null, amount: null,
        quantity: 0, type: '', reference: '',
        userid: this._use.active.Id, date: new Date(), muserid: null, mdate: null };
    }
  }

}
