import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { SetupService } from 'src/app/services/setup.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';

@Component({
  selector: 'app-setup-food',
  templateUrl: './setup-food.component.html',
  styleUrls: ['./setup-food.component.css']
})
export class SetupFoodComponent implements OnInit {
  @ViewChild('foodForm') public any: NgForm;
  edit: boolean; list: boolean; fd: any; food: any[]; res: any;
  constructor(private _setup: SetupService, private _use: NavComponent) { }

  ngOnInit() {
    this.formReset();
    this.getAllFood();
  }

  getAllFood() {
    this._setup.getFoods().subscribe((data: any) => {
      this.food = data;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  editFd(data) {
    // this.fd = this.sequence.find(s => s.sequenceId === id).
    this.fd = data;
    this.edit = true; this.list = true;
  }
  delFd(dat) {
    this._setup.delFood(dat.foodid).subscribe((data: any) => {
      this.res = data; this.food.slice(dat);
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  fileUpload(event) {
    // console.log(event);
    this._setup.Upload(event.target.files[0], 'Food').subscribe((data: any) => {
      this.fd.image = data; console.log(data);
    });
  }

  postFood() {
    this._setup.getCode('Food').subscribe((data: any) => {
      this.fd.code = data; this.addFood(this.fd);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Generate New Code for Food');
    });
  }

  addFood(food) {
    this._setup.postFood(food).subscribe((data: any) => {
      this.res = data; this.food.push(data);
      this.formReset(this.any); this.list = false;
      this._use.showMsg('success', 'Food Added', 'Added to list Successfully');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Add New Food');
    });
  }

  putFood() {
    this.fd.muserid = this._use.active.Id; this.fd.mdate = new Date();
    this._setup.putFood(this.fd.foodid, this.fd).subscribe((data: any) => {
      this.res = data; this.getAllFood();
      this.formReset(this.any); this.list = false;
      this._use.showMsg('success', 'Food Updated', 'Details Updated Successfully');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Updated Fail', 'Unable to Update Details');
    });
  }

  formReset(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.fd = { foodid: 0, code: '', name: '', image: '', description: '',
        price: 0, discount: 0, userid: this._use.active.Id, date: new Date(), muserid: null, mdate: null };
    }
  }
  listView() {
    this.list = true; this.edit = false;
  }
  back() {
    this.list = false;
  }
}
