import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HubConnectionBuilder, HttpTransportType } from '@aspnet/signalr';
import { NavComponent } from 'src/app/traits/nav/nav.component';
import { config } from 'src/environments/environment';
import { TellerService } from 'src/app/services/teller.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {
  list: boolean; order: any; lists: any[]; orderlist: any[];
  app = config; type = HttpTransportType.ServerSentEvents;
  private _hubConnection = new HubConnectionBuilder().withUrl(this.app.ordUrl,
    { transport: this.type }).build();
  constructor(public _nav: NavComponent, private _api: TellerService) { }

  ngOnInit() {
    this.formReset();
    this.getAllOrder();
    // this.getData();
    // this._hubConnection.start().then(() => {
    //   console.log('Hub connection started');
    //   this.getOrders();
    // }).catch(err => {
    //   console.log('Error establishing connection');
    // });
  }

  public getData(): void {
    this._hubConnection.on('pending', (data: any[]) => {
      console.log(data);
      this.lists = data;
    });
    this._hubConnection.on('order', (data: any[]) => {
      console.log(data);
      this.lists.push(data);
    });
    this._hubConnection.on('send', (data: any) => {
      console.log(data); this.getAllOrder();
      this._nav.showMsg('success', 'Broadcast', data);
    });
    // this._hubConnection.on('Error', (data: any) => {
    //   this._nav.showMsg('error', 'Error Message', data);
    // });
  }

  public getOrders(): void {
    this._hubConnection.invoke('orders');
    this._hubConnection.invoke('SendAsync', 'Orders List');
  }

  getAllOrder() {
    this._api.getOrderScreen().subscribe((data: any) => {
      this.lists = data.pending;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  getOrder(dat) {
    this.order = dat;
    this._api.getOrderbyCode(dat.orderno).subscribe((data: any) => {
      this.orderlist = data.list; this.order = data.order;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  cancelOrder(id) {
    this._api.cancelOrder(id).subscribe((data: any) => {
      this.order = data;
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  acceptOrder(id) {
    this._api.acceptOrder(id).subscribe((data: any) => {
      this.order = data; this.getAllOrder();
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  readyOrder(id) {
    this._api.readyOrder(id).subscribe((data: any) => {
      this.order = data; this.getAllOrder();
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

  formReset(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.order = {
        orderid: '', orderno: '', fullname: '', mobile: '', total: 0, status: 'pending', date: new Date,
        location: { name: '', Discount: 0} };
    }
  }
}
