import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { InventoryService } from 'src/app/services/inventory.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-additem',
  templateUrl: './additem.component.html',
  styleUrls: ['./additem.component.css']
})
export class AdditemComponent implements OnInit {
  // edit: boolean = false; add: boolean = false; item:any;
  @ViewChild('itemForm') public any: NgForm;
  edit: boolean; item: any; id: any; res: any; img: any;
  constructor(private _api: InventoryService, private _use: NavComponent, private _nav: Router, private _route: ActivatedRoute) { }

  ngOnInit() {
    this.formReset();
    this.getAll();
  }

  getAll() {
    this.id = this._route.snapshot.paramMap.get('id');
    this.getItem(this.id); // console.log(this.id);
  }

  getItem(id: any) {
    if (id && id !== 0) {
      this.edit = true;
      this._api.getItem(id).subscribe((data: any) => {
        this.item = data;
      }, (err: HttpErrorResponse) => { alert(err.statusText); });
    } else {
      this.edit = false;
      this.formReset();
    }
  }

  postItem() {
    this._api.getCode('Item').subscribe((data: any) => {
      this.item.code = data; this.addItem(this.item);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Generate New Code for Item');
    });
  }

  addItem(item) {
    this._api.postItem(item).subscribe((data: any) => {
      this.res = data; this.formReset(this.any);
      this._use.showMsg('success', 'Item Added', 'Added to list Successfully');
      this._nav.navigate(['/inventory/items']);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Adding Fail', 'Unable to Add New Item');
    });
  }

  putItem() {
    this.item.muserid = this._use.active.Id; this.item.mdate = new Date();
    this._api.putItem(this.item.itemid, this.item).subscribe((data: any) => {
      this.res = data; this.formReset(this.any);
      this._use.showMsg('success', 'Item Updated', 'Details Updated Successfully');
      this._nav.navigate(['/inventory/items']);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Updated Fail', 'Unable to Update Details');
    });
  }

  formReset(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.item = {
        itemid: 0, name: '', code: '', description: '', price: 0, saleprice: 0,
        userid: this._use.active.Id, date: new Date(), muserid: null, mdate: null };
    }
  }

}
