import { SetupTellerComponent } from './views/setup/setup-teller/setup-teller.component';
import { FinanceTransferComponent } from './views/finance/finance-transfer/finance-transfer.component';

export function tokenGetter() {
  return JSON.parse(localStorage.getItem('Token'));
}

@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    FootComponent,
    DashboardComponent,
    TellerPlaceorderComponent,
    TellerTransferComponent,
    TellerVoucherComponent,
    TellerSummaryComponent,
    OrdersComponent,
    EmployeeComponent,
    FinanceDaybookComponent,
    FinanceCashbookComponent,
    FinancePaymentComponent,
    FinanceSummaryComponent,
    FinanceReportComponent,
    FinancePayrollComponent,
    SetupSequenceComponent,
    SetupNorminalComponent,
    SetupUserComponent,
    SetupFoodComponent,
    SetupSmslogsComponent,
    LoginComponent,
    InventoryItemComponent,
    InventoryStockComponent,
    InventoryIssueComponent,
    InventorySupplyComponent,
    AdditemComponent,
    AddstockComponent,
    AddemployeeComponent,
    SetupSmsComponent,
    OrderListComponent,
    NotfoundComponent,
    SetupTransitComponent,
    SetupDiscountComponent,
    SetupLocationComponent,
    ForgetPasswordComponent,
    SetupTellerComponent,
    FinanceTransferComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    CommonModule,
    JwtModule.forRoot({
      config: {
        tokenGetter: tokenGetter,
      }
    }),
    ToastrModule.forRoot({
      timeOut: 10000,
      positionClass: 'toast-top-right',
      preventDuplicates: false,
      progressBar: true
    }),
    RouterModule.forRoot(appRoute)
  ],
  providers: [ SigninService, SetupService, InventoryService, TellerService,
    AuthGuard, ExitpageGuard,
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true } ],
  bootstrap: [AppComponent]
})
export class AppModule { }
