import { Component, OnInit } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { InventoryService } from 'src/app/services/inventory.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';

@Component({
  selector: 'app-inventory-stock',
  templateUrl: './inventory-stock.component.html',
  styleUrls: ['./inventory-stock.component.css']
})
export class InventoryStockComponent implements OnInit {
  stocklogs: any[]; stock: any[]; res: any;
  constructor(private _ser: InventoryService, private _use: NavComponent) { }

  ngOnInit() {
    this.getAllStock();
  }

  getAllStock() {
    this._ser.getStockLogs().subscribe((data: any) => {
      this.stocklogs = data;
    }, (err: HttpErrorResponse) => {
        this._use.showMsg('error', 'Stock Log Fetching Fail', 'Unable to Fetch Stock Log from database'); });
  }

  delStock(dat) {
    this._ser.delStock(dat.stockid).subscribe((data: any) => {
      this.res = data; this.stocklogs.slice(dat);
    }, (err: HttpErrorResponse) => { alert(err.statusText); });
  }

}
