import { Component, OnInit } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { TellerService } from 'src/app/services/teller.service';
import { NavComponent } from 'src/app/traits/nav/nav.component';
import { DatePipe } from '@angular/common';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-teller-summary',
  templateUrl: './teller-summary.component.html',
  styleUrls: ['./teller-summary.component.css']
})
export class TellerSummaryComponent implements OnInit {
  data: any; cash: any; search: any; lists: any[] = [];
  constructor(private _api: TellerService, private _use: NavComponent) { }

  ngOnInit() {
    this.init();
    this.getTodaySummary();
  }

  getTodaySummary() {
    const id = this._use.active.Id; const date = this._use.date;
    this._api.getOrderSummary(id, date).subscribe((data: any) => {
      this.cash = data; this.lists = data.trans; console.log(data);
      this._use.showMsg('success', 'Orders Summary', 'Successfully fetch current Order Summary');
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Orders Summary Fail', 'Unable to Get Current Balance'); });
  }

  getSummary(value) {
    this._api.getOrderSummary(this._use.active.Id, value).subscribe((data: any) => {
      this.cash = data.cash; this.lists = data.trans;
      this._use.showMsg('success', 'Orders Summary', 'Successfully fetch Teller Balance for Date: ' + value);
    }, (err: HttpErrorResponse) => {
      this._use.showMsg('error', 'Orders Summary Fail', 'Unable to Get Summary for Date: ' + value); });
  }

  init(form?: NgForm) {
    if (form != null) {
      form.reset();
    } else {
      this.lists = [];
      this.cash = { opening: 0, credit: 0, debit: 0, balance: 0 };
      this.search = { data: '', dat: null };
      // this.date = new Date();
    }
  }
}
